/* src/App.css */
.app {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  color: #333;
  padding: 20px;
}

.app-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.upload-form {
  margin: 20px 0;
}

.transcription-display {
  margin: 20px 0;
  text-align: left;
}

.transcription-segment {
  margin-bottom: 20px;
}

.words {
  display: flex;
  flex-wrap: wrap;
}

.word {
  margin-right: 5px;
  padding: 2px 4px;
  background-color: #e0e0e0;
  border-radius: 3px;
}
